<script>
// import myUpload from 'vue-image-crop-upload'
export default {
  name: 'UploadModal',
  props: ['openDialog'],
  data: function() {
    return {
      // dialog: this.openDialog ? this.openDialog : false,
      valid: false,
      uploaded: false,
      uploading: false,
      interval: {},
      value: 0,
      show: false,
      rules: [
        (value) =>
          !value ||
          value.size < 5000000 ||
          'Video size should be less than 5 MB!'
      ],
      categories: ['', '','','',''],
      visibilty: ['Public', 'Private'],
      formData: {
        title: '',
        description: '',
        category: '',
        visibilty: ''
      },
      imgDataUrl: 'https://picsum.photos/500/300',
      params: {
        token: '123456798',
        name: 'avatar'
      },
      headers: {
        smail: '*_~'
      },
      generateDialog:false,
      viewType:'scroll',
      currStep:1,
      infringementDetected:false,
      infringementDetecting:false,
    }
  },
  computed: {
    dialog() {
      return this.openDialog
    }
  },
  methods: {
    async uploadVideo(e) {
      const { valid } = await this.$refs.provider.validate(e)

      if (!valid) return
      // TODO: Upload the file
      this.uploading = true
      this.interval = setInterval(() => {
        if (this.value === 100) {
          this.uploaded = true
          clearInterval(this.interval)
        }
        this.value += 10
      }, 100)
      // }
      // }
      this.infringementDetect()
    },
    submit() {
      if (this.$route.name === 'Dashboard')
        return this.$router.push('/studio/videos')
      console.log('submittied')
      this.closeModal()
    },
    closeModal() {
      this.$emit('closeDialog')
    },
    selectFile() {
      this.$refs.fileInput.$refs.input.click()
    },
    toggleShow() {
      this.show = !this.show
    },
    cropSuccess(imgDataUrl, field) {
      console.log('-------- crop success --------')
      console.log(field)
      this.imgDataUrl = imgDataUrl
    },
    infringementDetect(){
      this.infringementDetecting = true
      setTimeout(() => {
        this.infringementDetecting = false
        this.infringementDetected = true
      }, 5000);
    },
    
    uploadImage(){
      this.$refs.uploadImage.click();
    },
  },
  components: {
    // myUpload
  }
}
</script>

